.QuizList {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    background: -webkit-gradient(linear, left top, right top, from(#efd5ff), to(#515ada));
    background: -webkit-linear-gradient(left, #efd5ff 0%, #515ada 100%);
    background: -o-linear-gradient(left, #efd5ff 0%, #515ada 100%);
    background: linear-gradient(90deg, #efd5ff 0%, #515ada 100%);
    color: #ffffff;
}

.list {
    list-style: none;
    width: 500px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0;
    padding: 0;
}

.list li {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    border: 1px solid #ffffff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.list li + li {
    margin-top: 20px;
}

.list a {
    padding: 10px;
    color: #ffffff;
    width: 100%;
    text-decoration: none;
}


