.NavigationToggle {
    position: fixed;
    top: 20px;
    left: 20px;
    font-size: 20px;
    cursor: pointer;
    color: #ffffff;
    -webkit-transition: opacity, -webkit-transform .22s;
    transition: opacity, -webkit-transform .22s;
    -o-transition: opacity, transform .22s;
    transition: opacity, transform .22s;
    transition: opacity, transform .22s, -webkit-transform .22s;
    z-index: 100;
}

.NavigationToggle:hover {
    opacity: 0.7;
}

.NavigationToggle.open {
    left: 320px;
}



