.Quiz {
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(10%, rgba(120,212,215,1)), color-stop(29%, rgba(89,188,195,1)), color-stop(60%, rgba(112,111,175,1)));
    background: -webkit-linear-gradient(bottom, rgba(120,212,215,1) 10%, rgba(89,188,195,1) 29%, rgba(112,111,175,1) 60%);
    background: -o-linear-gradient(bottom, rgba(120,212,215,1) 10%, rgba(89,188,195,1) 29%, rgba(112,111,175,1) 60%);
    background: linear-gradient(0deg, rgba(120,212,215,1) 10%, rgba(89,188,195,1) 29%, rgba(112,111,175,1) 60%);
    color: #ffffff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.Quiz h1 {
    font-size: 25px;
    padding: 0 8px;
    text-align: center;
}

@media (max-width: 768px) {
    .Quiz {
        width: 100%;
    }

    .Quiz h1 {
        font-size: 20px;
    }
}


