.ActiveQuiz__data {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.ActiveQuiz__data p {
    margin: 0;
    padding: 10px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.ActiveQuiz {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    width: 500px;
    word-wrap: normal;
}

.ActiveQuiz__counter {
    padding: 8px 16px;
    border: 1px solid #ffffff;
    border-radius: 10px;
    margin-left: auto;
    margin-right: 10px;
}

@media (max-width: 768px) {
    .ActiveQuiz__data {
      -ms-flex-pack: center;
          justify-content: center;
    }
    
    .ActiveQuiz__data p {
        margin-bottom: 8px;
       text-align: center;
    }

    .ActiveQuiz {
        width: 100%;
    }
}


