.FinishedQuiz {
    padding: 20px;
    color: #ffffff;
    border: 2px solid #ffffff;
}

.FinishedQuiz ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.FinishedQuiz li {
    margin-bottom: 10px;
}

.error {
    color: rgba(240, 87, 108, 0.3);
}

.success {
    color: rgba(161, 240, 69, 0.3);
}

.FinishedQuiz i {
    margin-left: 8px;
}



