.Input {
    margin-bottom: 15px;
}

.Input label {
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: 700;
}

.Input input {
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #bebebe;
    padding: 8px;
    width: 100%;
    outline: 0;
    -webkit-transition: all .3;
    -o-transition: all .3;
    transition: all .3;
    border-radius: 5px;
}

.Input span {
    color: #f01f30;
    font-style: 12px;
    font-weight: 700;
}

.Input.invalid label {
    color: #f01f30;
} 



